.sidebar {
    flex: 1;
    border-right: 0.5px solid rgb(230, 227, 227);
    height: 100vh;
    background-color: white;
    .top {
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        .logo {
            font-size: 2rem;
            font-weight: bold;
            color: var(--primary-color);
        }
    }
    hr {
        height: 0;
        border: 0.5px solid rgb(230, 227, 227);
    }

    .center {
        padding-left: 10px;
        ul {
            list-style: none;
            .title {
                font-size: 1rem;
                font-weight: bold;
                color: #999;
                margin-top: 15px;
                margin-bottom: 5px;
            }

            li {
                display: flex;
                align-items: center;
                padding: 5px;
                cursor: pointer;

                &:hover {
                    background-color: #ece8ff;
                }

                .icon {
                    font-size: 1.8rem;
                    color: var(--primary-color);
                }

                span {
                    font-size: 1.3rem;
                    font-weight: 600;
                    color: #888;
                    margin-left: 10px;
                }
            }
        }
    }

    .bottom {
        display: flex;
        align-items: center;
        margin: 10px;

        .color-options {
            width: 20px;
            height: 20px;
            border-radius: 5px;
            border: 1px solid var(--primary-color);
            cursor: pointer;
            margin-right: 5px;

            &:nth-child(1) {
                background-color: whitesmoke;
            }

            &:nth-child(2) {
                background-color: #333;
            }
        }
    }
}
